.hero {
  background-color: #fef9c7;
  display: flex;
  height: 75vh;
  width: 100%;
}

.hadas {
  font-family: "Amatic SC";
  font-size: 60px;
}

.hero img {
  object-fit: cover;
  width: 100%;
  height: 75vh;
  position: absolute;
  z-index: 1;
  opacity: 0.8;
}

.hero p {
  font-family: "Amatic SC";
  font-size: 40px;
  margin-top: 5%;
  margin-left: 5%;
  color: #fce181;
  z-index: 2;
}

.mobile-break {
  display: none;
}

.personal {
  padding: var(--section-padding);
  background-color: #026670;
}

.personal h2,
.projects h1,
.cv h2 {
  font-size: 40px;
  display: inline-block;
  padding: 5px 10px;
  margin: 0 20px 25px 20px;
}

.about-me {
  display: flex;
}

.about-me img {
  margin: 0 20px 25px 20px;
  height: 200px;
  width: auto;
  border-radius: 265px 18px 235px 18px/18px 235px 18px 265px;
  box-shadow: 4px 8px 7px -2px hsla(0, 0%, 0%, 0.5);
}

.about-me p {
  color: #fff;
  padding: 0 25px;
}

.carousel-background {
  background-color: #0b0b0b;
  border-radius: 7%;
  opacity: 70%;
  width: 30%;
}

.cv {
  padding: var(--section-padding);
  background-color: #fce181;
}

.other-stuff {
  display: flex;
}

.work-experience,
.education,
.volunteer-work {
  padding: 15px;
}

.contact {
  background-color: #e7d8c9;
  /* padding: var(--section-padding); */
  text-align: center;

  height: 40vh;
}

.social {
  display: flex;
  justify-content: center;
  align-items: flex-end;
}

.social img {
  margin: 15px;
}

.social img:hover {
  opacity: 0.7;
}

.contact-info h2 {
  margin-bottom: 20px;
}
